<template>
	<v-card outlined>
		<v-data-table
			mobile-breakpoint="200"
			hide-default-footer
			:items-per-page="-1"
			:headers="orderHeaders"
			:items="orders"
			:sort-by="['buy_price']"
			:sort-desc="[true]"
			@click:row="clickOrderRow"
			dense
		>
			<template v-slot:item.shortSymbol="{ item }">
				<small>{{ item.shortSymbol }}</small>
			</template>

			<template v-slot:item.side="{ item }">
				<v-chip class="pa-1" :color="sideColor(item.side)" x-small>{{
					item.side
				}}</v-chip>
			</template>

			<template v-slot:item.buy_price="{ item }">
				<div class="">
					<small class="text--secondary">{{ item.bpt }} </small>
					<v-chip
						class="pa-1"
						:color="priceColor(item.bs)"
						x-small
					></v-chip>
					{{ item.buy_price }}
				</div>
			</template>

			<template v-slot:item.sell_price="{ item }">
				<div class="">
					<small class="text--secondary">{{ item.spt }} </small>
					<v-chip
						class="pa-1"
						:color="priceColor(item.ss)"
						x-small
					></v-chip>
					{{ item.sell_price }}
				</div>
			</template>
		</v-data-table>
	</v-card>
</template>

<script>
import HttpService from '@/services/HttpService'

export default {
	name: 'OrderTree',

	components: {},

	data: () => ({
		dialog: false,
		dialogDelete: false,
		orderHeaders: [
			{ text: '', value: 'shortSymbol', width: 45 },
			{ text: 'Side', value: 'side', width: 32 },
			{
				text: 'Buy',
				value: 'buy_price',
				width: 100,
			},
			{
				text: 'Sell',
				value: 'sell_price',
				width: 100,
			},
			{ text: 'P', value: 'price', width: 63 },
			{ text: 'Amt', value: 'amt' },
			{ text: 'Id', value: 'id' },
			{ text: '%', value: 'profit' },
		],

		editedItem: {
			symbol: '',
			buy_price: '',
			sell_price: '',
		},
		orders: [],
		positions: [],

		showPositionTable: false,
		showSnackBar: false,

		itemDialog: {
			show: false,
			mode: null,
			title: '',
		},
	}),

	computed: {},

	watch: {},

	created() {
	},

	methods: {

		setOrders(orders){
			this.orders = orders
		},

		priceColor(item) {
			let color = ''
			if (item === 'Created') {
				color = 'amber lighten-4'
			} else if (item === 'New') {
				color = 'amber lighten-2'
			} else if (item === 'Filled') {
				color = 'blue lighten-3'
			} else if (item === 'PartiallyFilled') {
				color = 'blue lighten-4'
			} else if (item === 'Cancelled') {
				color = 'red lighten-2'
			} else if (item === 'Ticker') {
				color = 'transparent'
			} else {
				color = 'transparent'
			}
			return color
		},

		sideColor(side) {
			let color = ''
			if (side === 'Buy') {
				color = 'green lighten-4'
			} else if (side === 'Sell') {
				color = 'red lighten-4'
			} else {
				color = 'white'
			}
			return color
		},


		clickOrderRow(row) {
			this.showSnackBar = true
			this.editedItem = row

			// console.log(row)

			this.editedItem.sell_percent = row.spt
			this.editedItem.buy_percent = row.bpt
			// this.editedItem.realPrice =
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},

		toFixed: function (val, num) {
			return Number(Number(val).toFixed(num))
		},
	},
}
</script>

